/*Font Controlling */

.btn {
  margin-top: 30px;
  padding: 10px 20px;
  border: 2px solid #17bf63;
  background-color: transparent;
  border-radius: 25px;
  transition: all linear 0.5s;
}

.btn a {
  font-family: Ubuntu;
  color: #17bf63;
  text-decoration: none;
  transition: all linear 0.5s;
  font-weight: 400;
}

.btn:hover {
  background-color: #17bf63;
  border: 2px solid transparent;
}

.btn:hover a {
  color: #15202b;
}

.overlaybttn {
  margin: 30px 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.overlaybttn .fa-arrow-circle-down,
.overlaybttn .fa-arrow-circle-up {
  color: #17bf63;
  color: #e0245e;
}

.overlaybttn a {
  color: white;
  color: #e0245e;
  display: inline-block;
  text-decoration: none;
  padding: 15px 20px;
  border-radius: 20px;
  background: #15202b;
  background: #cbb3b3;
  transition-duration: 0.3s;
